import React from 'react';
import ReactDOM from 'react-dom';
import Header from './Header.js';
import Footer from './Footer.js';
import imgRF from './imgRF.jpg';
import imgNsk from  './imgNsk.jpg';


class Container extends React.Component {
  constructor() {
    super();
    this.state = { 
      title: "Новосибирск, Россия",
      people: "2 798 170 чел. (2020 г.)"
    }
  }

  render() { 
    return <div>
    <Header page={this.state.title}/>
    <div>{this.state.people}</div> 
    <img src={imgRF}  width="30" alt="Флаг России"> </img>
    <img src={imgNsk}  width="30" alt="Флаг Новосибирска"> </img>
    <Footer/>
     </div>
  };
}

ReactDOM(<Container />, document.getElementById('root'));
